<template>
    <div>
        <h1>Params</h1>
        
        <h2>Params로 받은 데이터</h2>
        <h2>name : {{$route.params.name}}</h2>
        <h2>age : {{$route.params.age}}</h2>

        <h2>props로 받은 데이터</h2>
        <h2>name : {{name}}</h2>
        <h2>age : {{age}}</h2>
    </div>
</template>
<script>
export default {
    name : 'Params',
    props : {
        name: {
            type: String,
            default: '',
        },
        age: {
            type: Number,
            default: 0,
        },
    },
    created() {
        console.log(this.$route.params.name);
        console.log(this.$route.params.age);
    }
}
</script>